body {
  font-family: Helvetica, sans-serif;
}

h1, h2, h3 { margin: 0.5rem 0;}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.2rem;
}

h3 {
  font-size: 1rem;
}

a {
  text-decoration: none;
}

.row {
  display: flex;
  justify-content: space-between;
}

.col-1 {
  flex: 1;
}

.col-2 {
  flex: 2;
}

.block {
  background-color: grey;
  padding: 1rem;
  margin: 0.5rem;
}

.row.center {
  align-items: center;
}